<template>
  <div class="container mx-32 mt-16">
    <h1 class="text-2xl">Taskify</h1>
    <div class="flex flex-col">
      <p class="text-sm">Dead simple task manager.</p>
      <p class="text-sm">
        List is stored inside
        <code>localStorage</code> so you can access it anytime.
      </p>
    </div>
    <div id="task-container" class="my-8 w-1/3">
      <form @submit.prevent="store()">
        <input
          type="text"
          class="bg-gray w-full py-2 mb-3"
          placeholder="What's next?"
          v-model="input"
        >
      </form>
      <hr>
      <div v-if="list.length == 0">
        <p class="opacity-75 text-sm">Great job, you're clean! 😍️</p>
      </div>
      <div v-for="(task, index) in list" :key="index" class="mb-2">
        <button
          @dblclick="remove(task)"
          @click="markAsCompleted(task)"
          :class="{ 'completed': task.completed }"
        ># {{ task.task }}</button>
      </div>
      <button
        class="mt-8 uppercase text-sm mr-3"
        @click="clearAll()"
        v-if="list.length > 0"
      >Clear all</button>
      <button class="mt-8 uppercase text-sm" @click="restore()">Restore</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      input: null,
      list: []
    };
  },
  mounted() {
    this.list = localStorage.getItem("list")
      ? JSON.parse(localStorage.getItem("list"))
      : [];
  },
  methods: {
    store() {
      this.list.push({
        task: this.input,
        completed: false
      });
      localStorage.setItem("list", JSON.stringify(this.list));
      this.input = null;
    },
    markAsCompleted(todo) {
      todo.completed = !todo.completed;
      localStorage.setItem("list", JSON.stringify(this.list));
    },
    remove(task) {
      let index = this.list.indexOf(task);
      localStorage.setItem("list_backup", JSON.stringify(this.list));
      this.list.splice(index, 1);
    },
    clearAll() {
      localStorage.clear();
      localStorage.setItem("list_backup", JSON.stringify(this.list));
      this.list = [];
    },
    restore() {
      this.list = [];
      let data = JSON.parse(localStorage.getItem("list_backup"));
      localStorage.setItem("list", JSON.stringify(data));
      this.list = data;
    }
  }
};
</script>


<style scoped>
.completed {
  text-decoration: line-through;
  color: grey;
}
</style>
